body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 12px;
  width: 400px;
  border-radius: 2px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
  margin-bottom: 16px;
  background: #e1efff;

  /* border: 1px solid #1d1919; */
}
.form-label {
  display: flex;
  flex-direction: column;

  margin-bottom: 16px;
  font-size: 20px;
  color: #171718;
  cursor: pointer;
}
.form-input {
  color: #171718;
  font: inherit;
  font-size: 1.2rem;
  padding: 5px;
  border-radius: 4px;
  border: 1px solid #bdbdbd;
  /* width: 100%; */
  outline: 0;
}
.form-input:focus {
  border: 1px solid #2b32b2;
}

.form-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background-color: #3f51b5;
  border: 0;
  outline: 0;
  padding: 8px 24px;
  border-radius: 4px;
  cursor: pointer;
  box-shadow: 0px 2px 1px 1px rgba(0, 0, 0, 0.05);
  font: inherit;
  text-transform: uppercase;
  user-select: none;
  transition: background-color 200ms ease-in-out;
}

.ContactList {
  max-width: 400px;
  padding: 0;
  margin-left: auto;
  margin-right: auto;
}

.ContactList-item {
  display: flex;
  align-items: center;
  padding: 12px;

  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
  background: #eef4fa;
}

.ContactList-item + .ContactList-item {
  margin-top: 12px;
}

.ContactList-text {
  /* flex: 0 0 65%; */
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 16px;
  border-radius: 3px;
}

.ContactList-actions {
  display: flex;
  justify-content: center;

  /* flex-grow: 1; */
}

.ContactList-button {
  margin-right: 4px;
  margin-left: 4px;
}
